.hp__hero {
    min-height: 100vh;
    // background-position: center center;
    // background-repeat: no-repeat;
    // background-size: cover;
    // position: relative;
    &--slider {
        width: 100%;
        height: 100vh;
    }
    &--slide.slick-slide {
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        height: 100vh;
    }
    &--container {
        position: absolute;
        top: 50%;
        left: 15%;
        transform: translateY(-50%);
    }
    &--text {
        background-color: rgba(255,255,255, 0.8);
        min-width: 250px;
        max-width: 50%;
        min-height: 250px;
    }
    &--arrow {
        position: absolute;
        left: 50%;
        bottom: 25px;
        transform: translateX(-50%) rotate(45deg);
        height: 20px;
        width: 20px;
        border-bottom: 2px solid #000;
        border-right: 2px solid #000;
        cursor: pointer;
    }
}

#carousel {
    position: absolute;
    height: 400px;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    overflow: hidden;
    img {
        max-width: inherit;
    }
    div {
        position: absolute;
        transition: transform 1s, left 1s, opacity 1s, z-index 1s;
        opacity: 1;
        img {
            width: 200px;
            transition: width 1s;
            @include media($mobile-up) {
                width: 400px;
            }
        }
        &:not(.selected) a {
            cursor: default;
        }
        &.hideLeft {
            left: 0%;
            opacity: 0;
            transform: translateY(50%) translateX(-50%);
            img {
                width: 100px;
                @include media($mobile-up) {
                    width: 200px;
                }
            }
        }
        &.hideRight {
            left: 100%;
            opacity: 0;
            transform: translateY(50%) translateX(-50%);
            img {
                width: 100px;
                @include media($mobile-up) {
                    width: 200px;
                }
            }
        }
        &.prev {
            z-index: 5;
            left: 30%;
            transform: translateY(25px) translateX(-50%);
            opacity: .6;
            img {
                width: 150px;
            }
            @include media($mobile-up) {
                transform: translateY(50px) translateX(-50%);
                img {
                    width: 300px;
                }
            }
        }
        &.prevLeftSecond {
            z-index: 4;
            left: 15%;
            transform: translateY(50%) translateX(-50%);
            opacity: .25;
            img {
                width: 100px;
                @include media($mobile-up) {
                    width: 200px;
                }
            }
        }
        &.selected {
            z-index: 10;
            left: 50%;
            transform: translateY(0px) translateX(-50%);
        }
        &.next {
            z-index: 5;
            left: 70%;
            transform: translateY(25px) translateX(-50%);
            opacity: .6;
            img {
                width: 150px;
            }
            @include media($mobile-up) {
                transform: translateY(50px) translateX(-50%);
                img {
                    width: 300px;
                }
            }
        }
        &.nextRightSecond {
            z-index: 4;
            left: 85%;
            transform: translateY(50%) translateX(-50%);
            opacity: .25;
            img {
                width: 100px;
                @include media($mobile-up) {
                    width: 200px;
                }
            }
        }
    }
}
